import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../Assect/logo.jpg'
import '../User/Na.css'
export default function AdminNavbar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
 <NavLink className="navbar-brand" to="/">
          <img src={logo} alt="Logo" width="30" height="30" />রায় ও মার্টিন
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink className="nav-link" to="/" exact>Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/userdetails">User Details</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/notice">Notice Board</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/check ">Time</NavLink>
            </li>
           
          </ul>
          
          {/* Add NavLink to go back to the User homepage */}
         
        </div>
      </div>
    </nav>
  );
}
