import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import './Login.css'
const CheckinForm = () => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    designation: '', // Set the initial value to an empty string
  });

  const [message, setMessage] = useState('');

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post('https://mernbackendfinal.onrender.com/ap1/check', formData); // Fixed the API endpoint URL.

      if (response.data.success) {
        setMessage(response.data.message);
        const token = response.data.token;
        localStorage.setItem('adminToken', token);
        navigate('/user');
      } else {
        setMessage('Error: ' + response.data.message);
      }
    } catch (error) {
      setMessage('Error: ' + error.message);
    }
  };

  return (
    <div>
      <h2>Check-in Form</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="name">Name:</label>
          <input
            type="text"
            id="name"
            name="name"
            value={formData.name}
            onChange={handleChange}
          />
        </div>
        <div>
          <label htmlFor="email">Email:</label>
          <input
            type="email"
            id="email"
            name="email"
            value={formData.email}
            onChange={handleChange}
          />
        </div>
        <div>
          <label htmlFor="designation">Designation:</label>
          <select
            id="designation"
            name="designation"
            value={formData.designation}
            onChange={handleChange}
          >
            <option value="">Select Designation</option>
            <option value="AccountExecutive">Account Executive</option>
          <option value="Telecaller">Telecaller</option>
          <option value="DTP">DTP</option>
          <option value="Artist">Artist</option>
          <option value="SalesManager">Sales Manager</option>
          <option value="SeniorSalesManager">Senior Sales Manager</option>
          <option value="SalesRepresentative">Sales Representative</option>
          <option value="CRO">CRO</option>
          <option value="CustomerCareHelpdesk">Customer Care Helpdesk</option>
           
          </select>
        </div>
        <button type="submit">Submit</button>
      </form>
      {message && <p>{message}</p>}
    </div>
  );
};

export default CheckinForm;
