import React, { useState } from 'react';
import * as Yup from 'yup';
import axios from 'axios';
import './Form.css';

function RegistrationForm() {
  const [formData, setFormData] = useState({
    name: '',
    password: '',
    email: '',
    Designation: '', // Initialize Designation
    DOB: '',
    BloodGroup: '',
    Address: '',
    AdharNO: '',
    ContactNo: '',
    EmergencyMobileNo: '',
  });

  const [submissionStatus, setSubmissionStatus] = useState(null);

  const validationSchema = Yup.object().shape({
    name: Yup.string().required('Name is required'),
    password: Yup.string().required('Password is required'),
    email: Yup.string().email('Invalid email format').required('Email is required'),
    Designation: Yup.string().required('Designation is required'),
    DOB: Yup.string().required('Date of Birth is required'),
    BloodGroup: Yup.string().required('Blood Group is required'),
    Address: Yup.string().required('Address is required'),
    AdharNO: Yup.string().required('Aadhar Number is required'),
    ContactNo: Yup.string().required('Contact Number is required'),
    EmergencyMobileNo: Yup.string().required('Emergency Mobile Number is required'),
  });

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      // Validate the form data
      await validationSchema.validate(formData, { abortEarly: false });

      // Assuming that your server is running on http://localhost:8000
      await axios.post('https://mernbackendfinal.onrender.com/ap1/register', formData);

      // Handle the response as needed
      console.log('Data submitted successfully');
      setSubmissionStatus('success');
    } catch (error) {
      // Handle errors and set the submission status to 'error'
      console.error('Error submitting data:', error);
      setSubmissionStatus('error');
    }
  };

  return (
    <form className="registration-form" onSubmit={handleSubmit}>
      <div className="form-group">
        <label>Name</label>
        <input
          type="text"
          name="name"
          value={formData.name}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Password</label>
        <input
          type="password"
          name="password"
          value={formData.password}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Email</label>
        <input
          type="email"
          name="email"
          value={formData.email}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Select Designation</label>
        <select name="Designation" value={formData.Designation} onChange={handleInputChange}>
          <option value="">Select Designation</option>
          <option value="AccountExecutive">Account Executive</option>
          <option value="Telecaller">Telecaller</option>
          <option value="DTP">DTP</option>
          <option value="Artist">Artist</option>
          <option value="SalesManager">Sales Manager</option>
          <option value="SeniorSalesManager">Senior Sales Manager</option>
          <option value="SalesRepresentative">Sales Representative</option>
          <option value="CRO">CRO</option>
          <option value="CustomerCareHelpdesk">Customer Care Helpdesk</option>
        </select>
      </div>

      <div className="form-group">
        <label>Date of Birth</label>
        <input
          type="date"
          name="DOB"
          value={formData.DOB}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Blood Group</label>
        <input
          type="text"
          name="BloodGroup"
          value={formData.BloodGroup}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Address</label>
        <input
          type="text"
          name="Address"
          value={formData.Address}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Aadhar Number</label>
        <input
          type="text"
          name="AdharNO"
          value={formData.AdharNO}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Contact Number</label>
        <input
          type="text"
          name="ContactNo"
          value={formData.ContactNo}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <label>Emergency Mobile Number</label>
        <input
          type="text"
          name="EmergencyMobileNo"
          value={formData.EmergencyMobileNo}
          onChange={handleInputChange}
        />
      </div>

      <div className="form-group">
        <button type="submit">Submit</button>
      </div>

      {submissionStatus === 'success' && (
        <p className="success-message">Data submitted successfully.</p>
      )}
      {submissionStatus === 'error' && (
        <p className="error-message">Error submitting data. Please try again.</p>
      )}
    </form>
  );
}

export default RegistrationForm;
