import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './NoticeList.css'; 
const NoticeList = () => {
  const [notices, setNotices] = useState([]);

  useEffect(() => {
    // Fetch all notices when the component mounts
    axios.get('https://mernbackendfinal.onrender.com/ap1/allnotices') // Replace with your API endpoint
      .then(response => {
        setNotices(response.data);
      })
      .catch(error => {
        console.error('Error fetching notices:', error);
      });
  }, []);

  return (
    <div>
      <h2>Notices</h2>
      <ul>
        {notices.map(notice => (
          <li key={notice._id}>
            <h3>{notice.title}</h3>
            <p>{notice.content}</p>
        
            <img src={notice.imageUrl} alt={notice.title} />
            <a href={notice.linkUrl}>Read More</a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default NoticeList;
