import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './UserList.css';

const UserList = () => {
  const [userDetails, setUserDetails] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    // Make the Axios request to fetch user details
    axios.get('https://mernbackendfinal.onrender.com/ap1/show') // Adjust the URL as needed
      .then((response) => {
        setUserDetails(response.data); // Assuming that the response is an array of user objects
        setLoading(false);
      })
      .catch((error) => {
        console.error(error);
        setLoading(false);
      });
  }, []); // Empty dependency array to run the effect only once

  return (
    <div className="id-card-container">
      {userDetails.map((user, index) => (
        <div key={index} className="id-card">
          <div className="id-card-header">
            <h2>{user.name}</h2>
            <p>{user.Designation}</p>
          </div>
          <div className="id-card-content">
            <p><strong>Email:</strong> {user.email}</p>
            <p><strong>DOB:</strong> {user.DOB}</p>
            <p><strong>Blood Group:</strong> {user.BloodGroup}</p>
            <p><strong>Address:</strong> {user.Address}</p>
            <p><strong>Adhar No:</strong> {user.AdharNO}</p>
            <p><strong>Contact No:</strong> {user.ContactNo}</p>
            <p><strong>Emergency Mobile No:</strong> {user.EmergencyMobileNo}</p>
          </div>
        </div>
      ))}
    </div>
  );
};

export default UserList;
