import React, { useState, useEffect } from 'react';
import axios from 'axios';

function NoticeController() {
  const [notices, setNotices] = useState([]);
  const [newNotice, setNewNotice] = useState({
    title: '',
    content: '',
    imageUrl: '',
    linkUrl: '',
  });
 
  const fetchNotices = async () => {
    try {
      const response = await axios.get('https://mernbackendfinal.onrender.com/ap1/allnotices');
      setNotices(response.data);
    } catch (error) {
      console.error('Error fetching notices:', error);
    }
  };

  useEffect(() => {
    fetchNotices();
  }, []);

  const createNotice = async () => {
    try {
      const response = await axios.post('https://mernbackendfinal.onrender.com/ap1/notices', newNotice);
      console.log('Notice created:', response.data);
      fetchNotices(); // Refresh the list of notices after creating one
    } catch (error) {
      console.error('Error creating notice:', error);
    }
  };

 
  const deleteAllNotices = async () => {
    if (window.confirm('Are you sure you want to delete all notices?')) {
      try {
        await axios.delete('https://mernbackendfinal.onrender.com/ap1/deletenotices');
        console.log('All notices deleted');
        fetchNotices(); // Refresh the list of notices after deleting all
      } catch (error) {
        console.error('Error deleting all notices:', error);
      }
    }
  };

  return (
    <div>
      <h2>Create a Notice</h2>
      <input
        type="text"
        placeholder="Title"
        value={newNotice.title}
        onChange={(e) => setNewNotice({ ...newNotice, title: e.target.value })}
      />
      <input
        type="text"
        placeholder="Content"
        value={newNotice.content}
        onChange={(e) => setNewNotice({ ...newNotice, content: e.target.value })}
      />
      <input
        type="text"
        placeholder="Image URL"
        value={newNotice.imageUrl}
        onChange={(e) => setNewNotice({ ...newNotice, imageUrl: e.target.value })}
      />
      <input
        type="text"
        placeholder="Link URL"
        value={newNotice.linkUrl}
        onChange={(e) => setNewNotice({ ...newNotice, linkUrl: e.target.value })}
      />
      <button onClick={createNotice}>Create</button>

      <button onClick={() => deleteAllNotices()}>Delete All</button>
      <h2>Notices</h2>
      <ul>
        {notices.map((notice) => (
          <li key={notice._id}>
            <p>ID: {notice._id}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default NoticeController;
