import React from 'react';
import './Card.css';
import one from '../Assect/1.jpg';
import two from '../Assect/2.jpg';
import three from '../Assect/3.jpg';

function Card() {
  return (
    <div className="card-container-outer">
      <div className="card-container">
        <div className="row">
          <div className="card">
            <img className="card-img-top" src={two} alt="Card image cap" />
            <div className="card-body">
              <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
            </div>
          </div>
          <div className="card">
            <img className="card-img-top" src={one} alt="Card image cap" />
            <div className="card-body">
              <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
            </div>
          </div>
          <div className="card">
            <img className="card-img-top" src={three} alt="Card image cap" />
            <div className="card-body">
              <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Card;
