import React from 'react';
import { NavLink } from 'react-router-dom';
import logo from '../Assect/logo.jpg'
import './Na.css'
export default function Navbar() {
  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container-fluid">
        <NavLink className="navbar-brand" to="/">
          <img src={logo} alt="Logo" width="30" height="30" />রায় ও মার্টিন
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <NavLink className="nav-link" to="/" exact>Home</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/registration">Registration</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/admin">Admin</NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/user"></NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/loging">User Login</NavLink>
            </li>
             <li className="nav-item">
              <NavLink className="nav-link" to="/dashboard"></NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link" to="/Check-in"></NavLink>
            </li>
          </ul>
          
        </div>
      </div>
    </nav>
  );
}
